<template>
	<view class="home-content">
		<!-- 头部轮播 -->
		<view class="carousel-section">
			<!-- 背景色区域 -->
			<swiper class="carousel" autoplay interval="3000" circular @change="swiperChange">
				<swiper-item v-for="(item, index) in swiperList" :key="index" class="carousel-item" @click="helpDaitl(item.url)">
					<image :src="item.src" />
				</swiper-item>
			</swiper>
			<!-- 自定义swiper指示器 -->
			<view class="swiper-dots">
				<text class="num">{{swiperCurrent+1}}</text>
				<text class="sign">/</text>
				<text class="num">{{swiperLength}}</text>
			</view>
		</view>
		
		<!-- 分类 -->
		<view class="cate-section">
			<view class="cate-item" v-for="(item, index) in sortList" :key="index" @click="detailShow(item.name, item.url)">
				<image :src="item.icon"></image>
				<text>{{item.name}}</text>
			</view>
		</view>
		

		<!-- 流程图 -->
		<view class="flow-section">
			<!-- <view class="flow-title">车检流程</view> -->
			<view>
				<video autoplay loop id="myVideo" :src="videoSrc"></video>
			</view>
		</view>
		
		<!-- 跳转登录页面 -->
		<uni-popup id="popup" ref="popup" type="center">
			<detail-dialog @closeDialog="closeDialog" @loginDefine="loginDefine"></detail-dialog>
		</uni-popup>
	</view>
</template>

<script>
	import {mapState} from 'vuex'
	import uniPopup from '@/components/uni-popup/uni-popup.vue'
	import detailDialog from '@/components/common/detail-dialog.vue'
	export default {
		components: {
			uniPopup,
			detailDialog
		},
		data() {
			return {
				swiperCurrent: 0,
				swiperLength: 0,
				swiperList: [], // 轮播数据
				sortList: [],  // 分类数据
				videoSrc: ''  // 视频播放地址
			}
		},
		computed: {
			...mapState(['hasLogin'])
		},
		onLoad() {
			this.loadData()
		},
		methods: {
			/**
			 * 请求后台数据
			 */
			async loadData() {
				uni.showLoading({
				    title: '加载中'
				});
				/* 请求轮播数据 */
				const swiperRes = await this.$myRequest({
					url: '/dtline/config/home_config',
					data: {
						configKey: 'swiper'
					}
				})
				if (swiperRes.code === 0) {
					const swiperList = JSON.parse(swiperRes.data[0].configContent)
					this.swiperLength = swiperList.length || 0;
					this.swiperList = swiperList || [];
					
				}
				
				/* 请求分类数据 */
				const sortRes = await this.$myRequest({
					url: '/dtline/config/home_config',
					data: {
						configKey: 'navigation'
					}
				})
				if (sortRes.code === 0) {
					const sortList = JSON.parse(sortRes.data[0].configContent)
					this.sortList = sortList || [];
				}
				
				// /* 请求播放视频地址 */
				const videoRes = await this.$myRequest({
					url: '/dtline/config/home_config',
					data: {
						configKey: 'video'
					}
				})
				if (videoRes.code === 0) {
					const videoSrc = videoRes.data[0].configContent
					this.videoSrc = videoSrc || ''
					// this.videoSrc = 'http://123.207.205.139:9090/video.mp4'
				}
				uni.hideLoading();
			},
			
			//轮播图切换修改背景色
			swiperChange(e) {
				const index = e.detail.current;
				this.swiperCurrent = index;
			},
			
			// 轮播图点击跳转
			helpDaitl(val) {
				uni.navigateTo({
					url: val
				})
				
			},
			
			// 关闭告警详情弹窗
			closeDialog() {
				this.$refs.popup.close()
			},
			
			// 跳转登录页面
			loginDefine() {
				this.$refs.popup.close()
				uni.navigateTo({
					url: '../login/login'
				})
			},
			
			// 分类点击
			detailShow(name, url) {
				switch(name) {
					case '我的车检':
					if (this.hasLogin) {
						uni.switchTab({
							url: url
						})	
					} else {
						this.$refs.popup.open()
					}
					
					break
					case '周边快修':
					uni.switchTab({
						url: url
					})
					break
					case '车检预约':
					uni.showToast({
					    title: '正在跳转浙里办',
						icon: 'loading',
					    duration: 2000
					});
					break
					case '违规违法':
					case '使用帮助':
					case '保险查询':
					uni.navigateTo({
						url: url
					})
					break
				}
			}
			
		}
	}
</script>

<style lang="scss">
	page {
		background-color: #fff!important;	
	}
	.home-content {
		/* 头部 轮播图 */
		.carousel-section {
			position: relative;
			
			.carousel {
				width: 100%;
				height: 380upx;
			
				.carousel-item {
					width: 100%;
					height: 100%;
					overflow: hidden;
				}
			
				image {
					width: 100%;
					height: 100%;
				}
			}
			
			.swiper-dots {
				display: flex;
				position: absolute;
				left: 50%;
				margin-left: -36upx;
				bottom: 30upx;
				width: 72upx;
				height: 36upx;
				background-image: url(data:image/png;base64,);
				background-size: 100% 100%;
			
				.num {
					width: 36upx;
					height: 36upx;
					border-radius: 50px;
					font-size: 24upx;
					color: #fff;
					text-align: center;
					line-height: 36upx;
				}
			
				.sign {
					position: absolute;
					top: 0;
					left: 50%;
					line-height: 36upx;
					font-size: 12upx;
					color: #fff;
					transform: translateX(-50%);
				}
			}
		}
		
		/* 分类 */
		.cate-section {
			display: flex;
			justify-content: space-around;
			align-items: center;
			flex-wrap:wrap;
			padding: 30upx 22upx; 
			background: #fff;
			border-bottom: 6px solid #f5f5f5;
			.cate-item {
				display: flex;
				flex-direction: column;
				align-items: center;
				font-size: $font-sm + 2upx;
				color: $font-color-dark;
			}
			image {
				width: 88upx;
				height: 88upx;
				margin-bottom: 14upx;
				border-radius: 50%;
				opacity: .7;
				box-shadow: 4upx 4upx 20upx rgba(250, 67, 106, 0.3);
			}
		}
		
		.flow-section {
			width: 100%;
			height: 640upx;
			background-color: #fff;
			video {
				width: 100%;
				height: 600upx;
				// border-radius: 4px;
			}
			
		}
	}
</style>
